body{
   background-color: #34495E;
}

.App{
  background-color: antiquewhite;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input{
  background-color: #CCD1D1 ;
  border: 0;
  width: 200px;
  height: 50px;
  padding : 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color:#34495E;

}

#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color:#909497 ;
  cursor: pointer;
  font-weight: bolder;
  color: #2C3E50;


}